import {
  Box,
  Card,
  CardActionArea,
  Grid,
  Hidden,
  Typography,
} from "@material-ui/core"
import { makeStyles } from "@material-ui/core/styles"
import { graphql, navigate, PageProps } from "gatsby"
import React from "react"
import { CategoryQuery, SitePageContext } from "../../graphql-types"
import Layout from "../components/Layout"
import SEO from "../components/seo"
import CategoryList from "../pages/indexPageComponents/CategoryList"
import CategorySelect from "../pages/indexPageComponents/CategorySelect"

// SitePageContext로부터 context를 이용해 전달한 값들의 type을 얻을 수 있다.
const Category: React.FC<PageProps<CategoryQuery, SitePageContext>> = ({
  data,
  pageContext,
}) => {
  const classes = useStyles()
  const siteTitle = data.site.siteMetadata?.title || `Title`
  const { category } = pageContext // gatsby-node.js의 createPage에서 넣어준 카테고리 이름.
  const { edges, totalCount } = data.allMarkdownRemark
  const tagHeader = `${totalCount} post${
    totalCount === 1 ? "" : "s"
  } categorized as "${category}"`
  return (
    <Layout siteTitle={siteTitle}>
      <SEO title={category} />
      <Grid container>
        <Grid item xs={12}>
          <Box className={classes.titleBox}>
            <Typography className={classes.categoryTitle} variant="h1">
              {tagHeader}
            </Typography>
            <Typography className={classes.subTitle}>
              Create What You Want to Use.
            </Typography>
          </Box>
        </Grid>
        <Hidden lgUp>
          <Grid item xs={12}>
            <CategorySelect selectedCategory={category} />
          </Grid>
        </Hidden>
        <Grid item xs={12} md={12} lg={9}>
          <Grid container className={classes.postListContainer}>
            {edges.map(({ node }) => {
              const title = node.frontmatter.title || node.fields.slug
              return (
                <Grid item xs={12} key={node.fields.slug}>
                  <Card
                    variant="outlined"
                    onClick={() =>
                      setTimeout(() => navigate(node.fields.slug), 300)
                    }
                    className={classes.postItemCard}
                  >
                    <CardActionArea className={classes.postItemCardArea}>
                      <Typography gutterBottom variant="h2">
                        {title}
                      </Typography>
                      <small>{node.frontmatter.date}</small>
                      <p
                        dangerouslySetInnerHTML={{
                          __html: node.frontmatter.description || node.excerpt,
                        }}
                        itemProp="description"
                      />
                    </CardActionArea>
                  </Card>
                </Grid>
              )
            })}
          </Grid>
        </Grid>
        <Hidden mdDown>
          <Grid item xs={12} md={12} lg={3} className={classes.categoryList}>
            <CategoryList selectedCategory={category} />
          </Grid>
        </Hidden>
      </Grid>
    </Layout>
  )
  // return (
  //   <Layout location={location} title={title}>
  //     <SEO title={title} />
  //     <h1>{tagHeader}</h1>
  //     <ul>
  //       {/* 각 카테고리에 해당하는 포스트의 목록을 출력한다. */}
  //       {edges.map(({ node }) => {
  //         const { slug } = node.fields
  //         const { title } = node.frontmatter
  //         return (
  //           <li key={slug}>
  //             <Link to={slug}>{title}</Link>
  //           </li>
  //         )
  //       })}
  //     </ul>
  //     <Link to="/">Go back to the homepage</Link>
  //   </Layout>
  // )
}

export default Category

// 쿼리의 argument인 $category는 page context로 전달 받는다.
export const pageQuery = graphql`
  query Category($category: String) {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(
      limit: 2000
      sort: { fields: [frontmatter___date], order: DESC }
      filter: { frontmatter: { categories: { in: [$category] } } }
    ) {
      totalCount
      edges {
        node {
          excerpt
          fields {
            slug
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            description
          }
        }
      }
    }
  }
`

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  categoryList: {
    position: "sticky",
    top: "75px",
    alignSelf: "flex-start",
  },
  postListContainer: {
    margin: theme.spacing(0, 0, 7, 0),
  },
  postItemCardArea: {
    padding: theme.spacing(3),
  },
  postItemCard: {
    margin: theme.spacing(1, 0, 0, 0),
  },
  titleBox: {
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    padding: theme.spacing(7, 0),
  },
  categoryTitle: {
    margin: theme.spacing(4, 0, 0, 0),
    overflow: "hidden",
    whiteSpace: "normal",
    fontSize: "48px",
    [theme.breakpoints.down("xs")]: {
      fontSize: "36px",
    },
    textAlign: "center",
  },
  subTitle: {
    margin: theme.spacing(-1, 0, 0, 0),
  },
}))
